import React from 'react';
import useEmblaCarousel from 'embla-carousel-react'
import { WheelGesturesPlugin } from 'embla-carousel-wheel-gestures'
import Card from '../Card/Card';
import styles from './CardCarousel.module.css';

const CardCarousel = ({ cardsData }) => {
  const [carouselRef, emblaApi] = useEmblaCarousel({
    align: "start",
    dragFree: true,
    rubberband: false,
  }, [
    WheelGesturesPlugin({
      forceWheelAxis: 'y',
    }),
  ]);

  if (emblaApi) emblaApi.on('scroll', (emblaApi) => {
    const {
      limit,
      target,
      location,
      offsetLocation,
      scrollTo,
      translate,
      scrollBody
    } = emblaApi.internalEngine()

    let edge = null

    if (location.get() >= limit.max) edge = limit.max;
    if (location.get() <= limit.min) edge = limit.min;

    if (edge !== null) {
      offsetLocation.set(edge)
      location.set(edge)
      target.set(edge)
      translate.to(edge)
      translate.toggleActive(false)
      scrollBody.useDuration(0).useFriction(0)
      scrollTo.distance(0, false)
    } else {
      translate.toggleActive(true)
    }
  });

  return (
    <div className={styles["carousel"]} ref={carouselRef}>
      <div className={styles["carousel__container"]}>
        {cardsData.map(card => (
          <div key={card.alt} className={styles["carousel__slide"]}>
            <Card src={card.src} {...card} />
          </div>
        ))}
      </div>
    </div>
  );
};

export default CardCarousel;